
@media only screen and (max-width: 767px) {
  .headersp{
    // height: 100%;
    height: 100%;
      position: fixed;
      z-index: 100;
      top: 0;
      left: 0;
      background: #ccc;
      color: #000;
      text-align: center;
      transform: translateY(-100%);
      transition: all 0.6s;
    &__list {
      // height: 100%;
      // position: fixed;
      // z-index: 100;
      // top: 0;
      // left: 0;
      // background: #ccc;
      // color: #000;
      // text-align: center;
      // transform: translateY(-100%);
      // transition: all 0.6s;
      // width: 100%;
      background: #ccc;
          margin: 20px auto;
          padding: 0;
          width: 100%;
      ul {
          // background: #ccc;
          // margin: 20px auto;
          // padding: 0;
          // width: 100%;
          font-size: 14px;
              list-style-type: none;
              // padding: 0;
              // width: 100%;
              border-bottom: 1px dotted #333;
          li {
              // font-size: 14px;
              // list-style-type: none;
              // // padding: 0;
              // // width: 100%;
              // border-bottom: 1px dotted #333;
              &:last-child {
                  padding-bottom: 0;
                  border-bottom: none;
              }
            a {
                display: block;
                color: #000;
                padding: 1em 0;
            }
          }
        }
    &.active {
        transform: translateY(0%);
    }
  }

    &__hum{
      display: block;
      position: fixed;
      right: 13px;
      top: 12px;
      width: 42px;
      height: 51px;
      cursor: pointer;
      z-index: 200;
      text-align: center;

      span{
        display: block;
        position: absolute;
        width: 30px;
        border-bottom: solid 3px rgb(138, 137, 137);
        -webkit-transition: .35s ease-in-out;
        -moz-transition: .35s ease-in-out;
        transition: .35s ease-in-out;
        left: 6px;

        &:nth-child(1) {
          top: 9px;
        }
        &:nth-child(2) {
          top: 18px;
        }
        &:nth-child(3) {
          top: 27px;
        }
        &:nth-child(4) {
          border: none;
          color: #eee;
          font-size: 9px;
          font-weight: bold;
          top: 34px;
        }
      }
      /* 最初のspanをマイナス45度に */
      &.active span:nth-child(1) {
          top: 18px;
          left: 6px;
          -webkit-transform: rotate(-45deg);
          -moz-transform: rotate(-45deg);
          transform: rotate(-45deg);
      }

      /* 2番目と3番目のspanを45度に */
      &.active span:nth-child(2),
      &.active span:nth-child(3) {
          top: 18px;
          -webkit-transform: rotate(45deg);
          -moz-transform: rotate(45deg);
          transform: rotate(45deg);
      }
    }
  }
}
